.container {
    display: flex;
    margin-top: 120px;
    justify-content: space-between;
    width: 100%;
    gap: 60px;
}

.menu {
    display: flex;
    flex-direction: column;
    gap: 80px;
    max-width: 320px;
}

.navigation {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.navlink {
    text-decoration: none;
    color: #8585ad;
    transition: opacity 0.5s ease;
}

.navlink:hover {
    opacity: 0.7;
}

.active {
    color: var(--primary);
}

.button {
    border: none;
    background-color: transparent;
    display: flex;
    padding: 0;
    color: #8585ad;
    cursor: pointer;
    transition: opacity 0.5s ease;
}

.button:hover {
    opacity: 0.7;
}

.text {
    margin: 0;
    color: #8585ad;
    opacity: 0.4;
}

.profile_info {
    width: 844px;
}