.summary__container {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.status_ready {
    width: 200px;
    max-height: 200px;
    overflow-y: hidden;
}

.status_in_progress {
    width: 200px;
    max-height: 200px;
    overflow-y: hidden;
}

.status {
    display: flex;
    gap: 36px;
}

.ready {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.ready_order {
    color: #0cc;
}

.in_progress {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.shadow {
    text-shadow: 0px 4px 32px rgba(51, 51, 255, 0.5), 0px 0px 8px rgba(51, 51, 255, 0.25),
    0px 0px 16px rgba(51, 51, 255, 0.25);
}