.container_main {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    max-height: 656px;
    position: relative;
}

.element_locked {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 40px;
}

.element_main {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.elementcontainer {
    overflow-y: scroll;
    max-height: 464px;
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}

.sum_container {
    display: flex;
    flex-direction: row;
